<template>
  <div class="checkout">
    <!--  -->
    <nav-bar>
      <h2></h2>
    </nav-bar>
    <!--  -->
    <div class="content">
      <div class="container">
        <!--  -->
        <div class="address">
          <div class="title"></div>
          <el-row :class="is_show ? 'active' : ''">
            <el-col 
              :class="['item', currentAddress === index ? 'active' : '']" 
              @click.native="currentAddress = index"
              :span="6" 
              v-for="(v, index) in address_list" 
              :key="v.id">
              <div class="name">{{v.name}}</div>
              <div class="tel">{{v.tel}}</div>
              <div class="address">{{v.address.join('')}}</div>
              <div class="full_address">{{v.full_address}}</div>
              <div class="btn">
                <div class="edit" @click="handleEdit(v)"></div>
                <div class="remove" @click="handleDeleteAddress(v.id)"></div>
              </div>
            </el-col>
            <el-col :span="6" class="add" @click.native="dialogVisible = true">
              <i class="el-icon-circle-plus-outline"></i>
              <span></span>
            </el-col>
          </el-row>
          <div class="more">
            <div class="down" v-if="!is_show" @click="is_show = true"><i class="el-icon-arrow-down"></i></div>
            <div class="up" v-else @click="is_show = false"><i class="el-icon-arrow-up"></i></div>
          </div>
        </div>
        <!--  -->
        <div class="goods-list">
          <div class="title"></div>
          <el-row>
            <el-col :span="24" v-for="item in payInfo.goods" :key="item.id">
              <div class="info">
                <div class="img"><img :src="item.goods_info.cover" width="100%" /></div>
                <div class="name">{{item.goods_info.name + ' ' +  item.version + ' ' + item.color}}</div>
              </div>
              <div class="unit_price">{{item.unit_price}} x {{item.count}}</div>
              <div class="price">{{item.unit_price * item.count}}</div>
            </el-col>
          </el-row>
        </div>
        <!--  -->
        <div class="distribution">
          <div class="title"></div>
          <span></span>
        </div>
        <!--  -->
        <el-row class="detail">
          <el-col>
            <div class="name"></div>
            <div class="money">{{payInfo.goods.length}}</div>
          </el-col>
          <el-col>
            <div class="name"></div>
            <div class="money">{{payInfo.total}}</div>
          </el-col>
          <el-col>
            <div class="name"></div>
            <div class="money">-0</div>
          </el-col>
          <el-col>
            <div class="name"></div>
            <div class="money">-0</div>
          </el-col>
          <el-col>
            <div class="name"></div>
            <div class="money">-0</div>
          </el-col>
          <el-col class="total">
            <div class="name"></div>
            <div class="money">{{payInfo.total}}</div>
          </el-col>
        </el-row>
        <!--  -->
        <div class="btn">
          <div class="back-cart" @click="$router.go(-1)"></div>
          <div class="settlement" @click="handleSettlement"></div>
        </div>
      </div>
    </div>

    <!--  -->
    <custom-footer />

    <!-- dialog -->
    <el-dialog
      :title="is_edit ? '' : ''"
      :visible.sync="dialogVisible"
      @close="handleDialogClose"
      width="40%">
      <el-form ref="form" :model="addressInfo" label-position="right" :rules="rules">
        <el-form-item label="" prop="name" label-width="80px">
          <el-input v-model="addressInfo.name"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel" label-width="80px">
          <el-input v-model="addressInfo.tel" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="address" label-width="80px">
          <el-cascader
            v-model="addressInfo.address"
            :options="city"
            :props="props"
            placeholder=" /  / "
            style="width: 100%"
            ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="full_address" label-width="80px">
          <el-input
            type="textarea"
            :rows="2"
            placeholder=""
            v-model="addressInfo.full_address">
          </el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleAddAddress"> </el-button>
      </template>
    </el-dialog>

  </div>
</template>

<script>
import CustomFooter from '../../components/customFooter/CustomFooter.vue';
// 
import NavBar from '../../components/navBar/NavBar.vue';
// /
import city from '../../utils/citydata';
  export default {
    data(){
      // 
      var validateTel = (rule, value, callback) => {
        // 
        if(value === '' || !value) callback(new Error(''));
        // 
        const reg = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
        // 
        if (!reg.test(value)) {
          return callback(new Error(''));
        }
        callback();
      };
      return {
        // 
        is_show: false,
        // 
        payInfo: {},
        // 
        address_list: [],
        // 
        currentAddress: 0,
        // dialog
        dialogVisible: false,
        // 
        is_edit: false,
        // id
        currentAddressId: 0,
        // 
        addressInfo: {
          name: '',
          tel: null,
          address: '',
          full_address: ''
        },
        // 
        city: city,
        // 
        props: { 

          expandTrigger: 'hover'
        },
        // 
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          tel: [
            { required: true, validator: validateTel, trigger: 'blur' }
          ],
          address: [
            { required: true, message: ' /  / ', trigger: 'blur' }
          ],
          full_address: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    created(){
      // title
      document.title = '';
      // 
      this.getCurrentPayInfo();
      // 
      this.getUserAddress();
    },
    methods: {
      // 
      getCurrentPayInfo(){
        this.payInfo = this.$store.state.currentPayInfo;
      },
      // 
      async getUserAddress(){
        const {data} = await this.$http.get(`/address/list/${this.$store.state.info.id}`);
        this.address_list = data;
      },
      // 
      handleEdit(row){
        // 
        const { id, name, tel, address, full_address } = row;
        this.addressInfo = {
          name,
          tel,
          address,
          full_address
        }
        // id
        this.currentAddressId = id;
        // dialog
        this.is_edit = true;
        this.dialogVisible = true;
      },
      // 
      handleAddAddress(){
        this.$refs.form.validate(async (valid) => {
          if (valid) {
            // 
            if (this.is_edit) {
              // 
              const { status } = await this.$http.put(`/address/update/${this.currentAddressId}`, this.addressInfo);
              // 
              if(status !== 200) return this.$message.error('');
              // dialog
              this.dialogVisible = false;
              this.$message.success('');
              // 
              this.is_edit = false;
            }else{
              // 
              const { status } = await this.$http.post(`/address/add/${this.$store.state.info.id}`, this.addressInfo);
              // 
              if(status !== 201) return this.$message.error('');
              // dialog
              this.dialogVisible = false;
              this.$message.success('');
            }
            // 
            this.getUserAddress();
          } else {
            return false;
          }
        });
      },
      // dialog
      handleDialogClose(){
        // form
        this.$refs.form.resetFields();
        // 
        this.addressInfo = {
          name: '',
          tel: null,
          address: '',
          full_address: ''
        }
        this.currentAddressId = 0;
        // 
        this.is_edit = false;
      },
      // 
      handleDeleteAddress(id){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const { status } = await this.$http.delete(`/address/delete/${id}`);
          // 
          if(status !== 200) return this.$message.error('');
          this.$message.success('');
          // 
          this.getUserAddress();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      // 
      async handleSettlement(){
        // 
        if(this.address_list.length === 0) return this.$message.error('');
        // 
        const obj = {
          goods: this.payInfo.goods,
          total: this.payInfo.total,
          address: this.address_list[this.currentAddress]
        }
        const { data, status } = await this.$http.post(`/pay/order/${this.$store.state.info.id}`, obj);
        // 
        if(status !== 201) return this.$message.error(data);
        // 
        this.$router.push(`/pay/${data.orderNumber}`);
      }
    },
    components:{
      NavBar,
      CustomFooter
    }
  }
</script>

<style lang="less" scoped>
.checkout{
  .content{
    padding: 40px 0 60px;
    .container{
      background-color: #fff;
      padding: 30px;
      .title{
        color: #333;
        font-size: 18px;
      }
      .address{
        .el-row{
          height: 228px;
          overflow: hidden;
          padding: 30px 0 0;
          .el-col{
            width: 268px;
            height: 168px;
            padding: 20px;
            margin-left: 30px;
            margin-bottom: 30px;
            box-sizing: border-box;
            border: 1px solid #e0e0e0;
            cursor: pointer;
          }
          .active{
            border: 1px solid #ff6700;
          }
          .item{
            color: #757575;
            font-size: 14px;
            position: relative;
            div{
              margin: 10px 0;
            }
            .name{
              margin-top: 0;
              font-size: 18px;
              color: #333;
              line-height: 30px;
              margin-bottom: 10px;
            }
            .btn{
              position: absolute;
              bottom: 0;
              left: 0;
              right: 10px;
              z-index: 33;
              height: 20px;
              display: none;
              div{
                width: 40px;
                font-size: 12px;
                color: #ff6700;
              }
            }
            &:hover{
              .btn{
                display: flex;
              }
            }
          }
          .add{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: #b0b0b0;
            font-size: 14px;
            cursor: pointer;
            i{
              font-size: 30px;
              margin-bottom: 5px;
            }
          }
        }
        .active{
          height: auto;
          overflow: auto;
        }
        .more{
          height: 50px;
          line-height: 50px;
          background-color: #eee;
          color: #424242;
          font-size: 14px;
          text-align: center;
          cursor: pointer;
        }
      }
      .goods-list{
        margin-top: 30px;
        border-bottom: 1px solid #e0e0e0;
        .el-row{
          padding: 20px 0;
          color: #424242;
          font-size: 14px;
          .el-col{
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            & > div{ 
              flex: 1; 
              text-align: center;
            }
            
            .info{
              display: flex;
              align-items: center;
              .img{
                width: 60px;
              }
            }
            .price{
              color: #ff6700;
            }
          }
        }
      }
      .distribution{
        height: 100px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #e0e0e0;
        .title{
          width: 120px;
        }
        span{
          color: #ff6700;
        }
      }
      .detail{
        height: 240px;
        margin: 0 20px;
        padding: 20px 0 0;
        border-bottom: 1px solid #e0e0e0;
        .el-col{
          height: 28px;
          display: flex;
          align-items: center;
          .name{
            width: 92%;
            text-align: right;
            color: #757575;
          }
          .money{
            flex: 1;
            text-align: right;
            color: #ff6700;
          }
        }
        .total{
          height: 80px;
          .money{
            font-size: 24px;
          }
        }
      }
      .btn{
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        div{
          width: 150px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          margin-left: 30px;font-size: 14px;
          cursor: pointer;
        }
        .back-cart{
          border: 1px solid #b0b0b0;
          color: #b0b0b0;
        }
        .settlement{
          background-color: #ff6700;
          color: #fff;
          &:hover{
            background-color: #ff5120;
          }
        }
      }
    }
  }
}
</style>